@mixin onLandscape {
  @media (max-width: 1024px) and (orientation: landscape) {
    @content;
  }
}

@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onUlta {
  @media (min-width: 1200px) {
    @content;
  }
}
